<template>
  <div class="artRoute">
      <mavon-editor v-model="mdValue"
      defaultOpen="preview"
      :editable="false"
      :toolbarsFlag="false"
      :subfield="false"
      />
  </div>
</template>

<script>
export default {
    name:'artRoute',
    data(){
        return{
            mdValue:'',
        }
    },
    methods:{
        async getArtData(main){
            // console.log(main.data.readingPath);
            let {data} = await this.$axios.get('/node' + main.data.readingPath)
            this.mdValue = data
            // console.log(this.mdValue);
        },
    },
    created(){
        this.getArtData(this.$route.query)
    },
}
</script>

